import React, { useState, useEffect } from "react";
import axios from "axios";
import SalesDetail from "../sales/salesDetailComponent";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faTrash,
  faFilter,
  faSearch,
} from "@fortawesome/free-solid-svg-icons"; // Importar ícone de busca
import { Sale } from "../../../domain/interfaces/sale-interface";

interface SalesListProps {
  salesData?: Sale[];
  idLoja: string;
  vendedoresDisponiveis?: { id_funcionario: string; nome: string }[];
}

const ITEMS_PER_PAGE = 5;

const SalesList: React.FC<SalesListProps> = ({
  salesData = [],
  idLoja,
  vendedoresDisponiveis = [],
}) => {
  const [allSales, setAllSales] = useState<Sale[]>([]);
  const [selectedSale, setSelectedSale] = useState<Sale | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [totalPages, setTotalPages] = useState<number>(1);
  const [showFilters, setShowFilters] = useState(false);
  const [filters, setFilters] = useState({
    forma_pagamento: "",
    data_inicio: "",
    data_fim: "",
    vendedor_id: "",
  });

  // --- INÍCIO DAS NOVAS ALTERAÇÕES ---
  // Estados para a busca de vendedor no filtro
  const [vendedorSearchTerm, setVendedorSearchTerm] = useState("");
  const [foundVendedores, setFoundVendedores] = useState<
    { id_funcionario: string; nome: string }[]
  >([]);
  const [showVendedorDropdown, setShowVendedorDropdown] = useState(false);
  // --- FIM DAS NOVAS ALTERAÇÕES ---

  const jwtToken = localStorage.getItem("jwtToken");

  useEffect(() => {
    const fetchSales = async () => {
      try {
        setLoading(true);
        setError(null);

        // --- ALTERAÇÃO AQUI: Correção no filtro de data ---
        // Garante que o filtro `data_fim` inclua todas as vendas do dia selecionado.
        const params = new URLSearchParams({
          page: currentPage.toString(),
          limit: ITEMS_PER_PAGE.toString(),
          ...(filters.forma_pagamento && {
            forma_pagamento: filters.forma_pagamento,
          }),
          ...(filters.data_inicio && { data_inicio: filters.data_inicio }),
          ...(filters.data_fim && { data_fim: `${filters.data_fim}T23:59:59` }), // Adiciona o fim do dia
          ...(filters.vendedor_id && { funcionario_id: filters.vendedor_id }),
        });

        const response = await axios.get(
          `http://localhost:9700/api/vendas/loja/${idLoja}/paginado?${params.toString()}`,
          {
            headers: { Authorization: `Bearer ${jwtToken}` },
          }
        );

        if (response.data?.success) {
          setAllSales(response.data.data || []);
          setTotalPages(response.data.totalPages || 1);
        }
      } catch (err) {
        console.error("Erro ao buscar vendas:", err);
        setError("Não foi possível carregar as vendas do servidor.");
      } finally {
        setLoading(false);
      }
    };

    fetchSales();
  }, [idLoja, currentPage, jwtToken, filters]);

  useEffect(() => {
    if (salesData && salesData.length > 0) {
      setAllSales((prevSales) => {
        const newSalesToAdd = salesData.filter(
          (newSale) => !prevSales.some((s) => s.id_venda === newSale.id_venda)
        );
        return [...newSalesToAdd, ...prevSales];
      });
    }
  }, [salesData]);

  // --- INÍCIO DAS NOVAS ALTERAÇÕES ---
  // Função para buscar vendedores no filtro
  const handleVendedorSearch = () => {
    const term = vendedorSearchTerm.toLowerCase().trim();
    if (term) {
      const results = vendedoresDisponiveis.filter((v) =>
        v.nome.toLowerCase().includes(term)
      );
      setFoundVendedores(results);
    } else {
      setFoundVendedores(vendedoresDisponiveis); // Mostra todos se a busca for vazia
    }
    setShowVendedorDropdown(true);
  };

  const handleSelectVendedor = (vendedor: {
    id_funcionario: string;
    nome: string;
  }) => {
    setFilters((prev) => ({ ...prev, vendedor_id: vendedor.id_funcionario }));
    setVendedorSearchTerm(vendedor.nome); // Preenche o input com o nome selecionado
    setShowVendedorDropdown(false);
  };
  // --- FIM DAS NOVAS ALTERAÇÕES ---

  const handleFilterChange = (
    e: React.ChangeEvent<HTMLSelectElement | HTMLInputElement>
  ) => {
    const { name, value } = e.target;
    setFilters((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  // --- ALTERAÇÃO AQUI: Atualiza a função de limpar filtros ---
  const resetFilters = () => {
    setFilters({
      forma_pagamento: "",
      data_inicio: "",
      data_fim: "",
      vendedor_id: "",
    });
    setVendedorSearchTerm("");
    setFoundVendedores([]);
    setShowVendedorDropdown(false);
    setCurrentPage(1);
  };

  const openModalWithSale = (sale: Sale) => {
    setSelectedSale(sale);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedSale(null);
  };

  const handlePageChange = (page: number) => {
    if (page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  const deleteSale = async (saleId: string) => {
    // ... (função sem alterações)
  };

  return (
    <div className="quinary p-5 pb-4 mb-5 mx-auto white-light-small w-75 rounded-5">
      <div className="mb-4 mx-auto d-flex justify-content-between align-items-center">
        <h5 className="mb-0 text-white">
          <i className="fas fa-list-ul mr-2"></i>Vendas Registradas
        </h5>
        <button
          className="btn btn-sm primaria"
          onClick={() => setShowFilters(!showFilters)}
        >
          <FontAwesomeIcon icon={faFilter} className="mr-2" />
          {showFilters ? "Ocultar Filtros" : "Filtrar"}
        </button>
      </div>

      {showFilters && (
        <div className="quartenary p-3 rounded-lg mb-4">
          <div className="row g-3">
            <div className="col-md-3">
              <label className="form-label text-white-75 small">
                Forma de Pagamento
              </label>
              <select
                name="forma_pagamento"
                className="form-select input-form"
                value={filters.forma_pagamento}
                onChange={handleFilterChange}
              >
                <option value="">Todas</option>
                <option value="DINHEIRO">Dinheiro</option>
                <option value="CARTAO_CREDITO">Cartão Crédito</option>
                <option value="CARTAO_DEBITO">Cartão Débito</option>
                <option value="PIX">PIX</option>
              </select>
            </div>

            {/* --- INÍCIO DA SEÇÃO MODIFICADA: Filtro de Vendedor --- */}
            <div className="col-md-3 position-relative">
              <label className="form-label text-white-75 small">Vendedor</label>
              <div className="input-group">
                <input
                  type="text"
                  className="form-control input-form"
                  placeholder="Buscar vendedor..."
                  value={vendedorSearchTerm}
                  onChange={(e) => setVendedorSearchTerm(e.target.value)}
                  onBlur={() =>
                    setTimeout(() => setShowVendedorDropdown(false), 200)
                  }
                />
                <button
                  className="btn btn-secondary"
                  type="button"
                  onClick={handleVendedorSearch}
                >
                  <FontAwesomeIcon icon={faSearch} />
                </button>
              </div>
              {showVendedorDropdown && (
                <ul
                  className="list-group position-absolute w-100 mt-1 z-index-dropdown bg-dark border border-secondary rounded shadow-sm"
                  style={{ maxHeight: "150px", overflowY: "auto" }}
                >
                  {foundVendedores.length > 0 ? (
                    foundVendedores.map((v) => (
                      <li
                        key={v.id_funcionario}
                        className="list-group-item bg-dark text-white cursor-pointer hover-light"
                        onMouseDown={() => handleSelectVendedor(v)} // onMouseDown para evitar conflito com onBlur
                      >
                        {v.nome}
                      </li>
                    ))
                  ) : (
                    <li className="list-group-item text-white bg-dark">
                      Nenhum vendedor encontrado
                    </li>
                  )}
                </ul>
              )}
            </div>
            {/* --- FIM DA SEÇÃO MODIFICADA --- */}

            <div className="col-md-3">
              <label className="form-label text-white-75 small">
                Data Início
              </label>
              <input
                type="date"
                name="data_inicio"
                className="form-control input-form"
                value={filters.data_inicio}
                onChange={handleFilterChange}
              />
            </div>

            <div className="col-md-3">
              <label className="form-label text-white-75 small">Data Fim</label>
              <input
                type="date"
                name="data_fim"
                className="form-control input-form"
                value={filters.data_fim}
                onChange={handleFilterChange}
              />
            </div>

            <div className="col-12 d-flex justify-content-end gap-2">
              <button
                className="btn btn-sm btn-outline-light"
                onClick={resetFilters}
              >
                Limpar Filtros
              </button>
              <button
                className="btn btn-sm primaria"
                onClick={() => setCurrentPage(1)}
              >
                Aplicar Filtros
              </button>
            </div>
          </div>
        </div>
      )}

      {/* O restante do componente (tabela, paginação, modal) permanece sem alterações */}
      {/* ... cole o restante do seu JSX aqui ... */}
    </div>
  );
};

export default SalesList;
